<template>
  <div class="container mt-5">
    <h2></h2>
    <br />
    <span class="errorMsg">{{errorMsg}}</span>
    <br />
    <div class="row">
      <div class="col-sm-8">
        <form @submit.prevent="signUp">
          <div class="form-group">
            <label for="displayName"></label>

            <input
              type="text"
              class="form-control"
              placeholder=""
              v-model="userName"
              autocomplete="off"
            />
          </div>

          <div class="form-group">
            <label for="email"></label>
            <input type="email" class="form-control" id="email" v-model="email" autocomplete="off" />
          </div>
          <div class="form-group">
            <label type="password"></label>
            <input type="password" class="form-control" id="password" v-model="password" />
          </div>
          <div class="form-group">
            <label type="password">()</label>
            <input type="password" class="form-control" id="password" v-model="password2" />
          </div>
          <button type="submit" class="btn btn-info"></button>
        </form>
        <p>
          <router-link to="/"></router-link>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
// import defaultImg from "@/assets/cat.png";

export default {
  name: "singUp",
  props: [],
  data: function () {
    return {
      email: "",
      password: "",
      password2: "",
      userName: "",
      isAuthenticated: false,
      errorMsg: "",
    };
  },

  mounted: function () {
    this.errorMsg = "";
  },

  methods: {
    /**
     * Cloud Firestore
     * email password displayName photoURL
     */
    signUp: function () {
      //
      if (!this.checkForm()) {
        return;
      }

      //Cloud Firestore
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.email, this.password)
        .then((result) => {
          result.user.updateProfile({
            displayName: this.userName,
          });

          //TBL
          this.createProfile(result.user.uid);

          //TBL
          this.createFavorites(result.user.uid);

          alert("");
          this.$router.push("/");
        })
        .catch((error) => {
          this.errorMsg = `${error.message}`;
        });
    },

    //
    createProfile: function (uid) {
      //Cloud Firestore
      firebase
        .firestore()
        .collection("profile")
        .doc(uid)
        .set({
          uid: uid,
          userName: this.userName,
          introduction: "",
          comment: "",
          photoURL:
            "https://firebasestorage.googleapis.com/v0/b/chatapp-fe0b0.appspot.com/o/img%2Fcat.png?alt=media&token=7b27d4f2-9248-4182-9ab7-89aeae9694f6",
        })
        .then(() => {
          console.log("created profile");
        })
        .catch((error) => {
          console.log(error);
        });
    },

    //TBL
    //Cloud Firestore
    createFavorites: function (uid) {
      firebase
        .firestore()
        .collection("favorites")
        .doc(uid)
        .set({
          fav_roomIds: [""],
          uid: uid,
          useName: this.userName,
        })
        .catch((error) => {
          console.log(error);
        });
    },

    /**
     * 
     * @return {boolean}
     */
    checkForm: function () {
      if (this.userName.length < 2) {
        this.errorMsg = "2";
        return false;
      }

      if (this.email === "") {
        return false;
      }

      if (this.password.length < 6) {
        this.errorMsg = "6";
        return false;
      }

      if (this.password !== this.password2) {
        this.errorMsg = "";
        return false;
      }

      return true;
    },
  },
};
</script>

<style scoped>
.container {
  padding-top: 200px;
}

.errorMsg {
  color: red;
  font-size: 28px;
}
</style>
